  .Modal {
    position: absolute;
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px;
    width: 60%;
    height: 90%;
    margin: auto;
    padding: 10px;
    background-color: white;
  }

  .Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(121, 134, 203, 0.3);

  }

  .button {
    color: rgba(121, 134, 203, 0.3);
  }

  .modal-content {
    margin: 20px;
  }

